import React, { useEffect, useState } from 'react';
import {
  StyledBtn,
  StyledCard,
  StyledCloseBtn,
  StyledDesc,
  StyledFirstDescWrapper,
  StyledHeaderWrapper,
  StyledImgWrapper,
  StyledSecondDescWrapper,
} from './Card.styled';
import { useDispatch, useSelector } from 'react-redux';
import { selectFavorites } from 'redux/selectors';
import { toggleFavorite } from 'redux/slice';
import { Modal } from 'components/Modal/Modal';

const Card = ({ car }) => {
  const favorites = useSelector(selectFavorites);
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [currentCar, setCurrentCar] = useState();
  const handleOpenModal = car => {
    setIsOpenModal(!isOpenModal);
    setCurrentCar(car);
  };
  useEffect(() => {
    isOpenModal
      ? (document.body.style.overflow = 'hidden')
      : (document.body.style.overflow = 'auto');
  }, [isOpenModal]);

  const dispatch = useDispatch();
  const handleToggleFavorite = item => {
    dispatch(toggleFavorite(item));
  };
  return (
    <>
      <StyledCard key={car.id}>
        <StyledCloseBtn onClick={() => handleToggleFavorite(car)}>
          {favorites?.map(item => item?.id).includes(car?.id) ? (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="18"
              viewBox="0 0 18 18"
              fill="none"
            >
              <path
                d="M15.63 3.4575C15.2469 3.07425 14.7921 2.77023 14.2915 2.56281C13.7909 2.35539 13.2543 2.24863 12.7125 2.24863C12.1706 2.24863 11.634 2.35539 11.1334 2.56281C10.6329 2.77023 10.178 3.07425 9.79497 3.4575L8.99997 4.2525L8.20497 3.4575C7.4312 2.68373 6.38174 2.24903 5.28747 2.24903C4.19319 2.24903 3.14374 2.68373 2.36997 3.4575C1.5962 4.23127 1.1615 5.28072 1.1615 6.375C1.1615 7.46927 1.5962 8.51873 2.36997 9.2925L3.16497 10.0875L8.99997 15.9225L14.835 10.0875L15.63 9.2925C16.0132 8.90943 16.3172 8.45461 16.5247 7.95401C16.7321 7.45342 16.8388 6.91686 16.8388 6.375C16.8388 5.83313 16.7321 5.29657 16.5247 4.79598C16.3172 4.29539 16.0132 3.84056 15.63 3.4575Z"
                fill="#3470FF"
                stroke="#3470FF"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          ) : (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="18"
              viewBox="0 0 18 18"
              fill="none"
            >
              <path
                d="M15.63 3.4575C15.2469 3.07425 14.7921 2.77023 14.2915 2.56281C13.7909 2.35539 13.2543 2.24863 12.7125 2.24863C12.1706 2.24863 11.634 2.35539 11.1334 2.56281C10.6329 2.77023 10.178 3.07425 9.79497 3.4575L8.99997 4.2525L8.20497 3.4575C7.4312 2.68373 6.38174 2.24903 5.28747 2.24903C4.19319 2.24903 3.14374 2.68373 2.36997 3.4575C1.5962 4.23127 1.1615 5.28072 1.1615 6.375C1.1615 7.46927 1.5962 8.51873 2.36997 9.2925L3.16497 10.0875L8.99997 15.9225L14.835 10.0875L15.63 9.2925C16.0132 8.90943 16.3172 8.45461 16.5247 7.95401C16.7321 7.45342 16.8388 6.91686 16.8388 6.375C16.8388 5.83313 16.7321 5.29657 16.5247 4.79598C16.3172 4.29539 16.0132 3.84056 15.63 3.4575Z"
                stroke="white"
                strokeOpacity="0.8"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          )}
        </StyledCloseBtn>
        <StyledImgWrapper $url={car?.img}></StyledImgWrapper>
        <StyledHeaderWrapper>
          <p>
            {car.make} <span>{car.model}</span>, {car.year}
          </p>
          <p>{car.rentalPrice}</p>
        </StyledHeaderWrapper>

        <StyledFirstDescWrapper>
          <StyledDesc>{car.address.split(',')[1]}</StyledDesc>
          <StyledDesc>{car.address.split(',')[2]}</StyledDesc>
          <StyledDesc>{car.rentalCompany}</StyledDesc>
          {/* <StyledDesc>Premium</StyledDesc> */}
        </StyledFirstDescWrapper>
        <StyledSecondDescWrapper>
          <StyledDesc>{car.type}</StyledDesc>
          <StyledDesc>{car.model || car.make}</StyledDesc>
          <StyledDesc>{car.id}</StyledDesc>
          <StyledDesc>{car.functionalities[0]}</StyledDesc>
        </StyledSecondDescWrapper>

        <StyledBtn onClick={() => handleOpenModal(car)}>Learn more</StyledBtn>
      </StyledCard>
      {isOpenModal && <Modal close={handleOpenModal} car={currentCar}></Modal>}
    </>
  );
};

export default Card;

